import React, {  useEffect, useState } from 'react';
import Mobile from './Mobile';
import Pc from './Pc';

function App() {
  const [width,setWidth] = useState(window.innerWidth);
  console.log(width)
  console.log(window.innerWidth)
  useEffect(()=>{
    setWidth(window.innerWidth)
  },[window.innerWidth])
  return (
   <div>
     {width>=500 ? <Pc></Pc> : <Mobile></Mobile>}
   </div>  );
}

export default App;
